import Pagination from "./assembly_example/Pagination.vue";
// 表格
import Formtable from "./assembly_example/Formtable.vue";
//宽高过渡
import TransitionH from "./assembly_example/TransitionH.vue";
import { computed } from "vue";
export default {
  components: {
    NumAnimation,
    Button,
    Confirm,
    Tips,
    ScrollContainer,
    Pagination,
    Formtable,
    Explain,
    TransitionH,
  },
  props: {
    navitem: {
      //总页数  要比范围大2倍+2 ##############
      type: String,
      default: "",
    },
  },
  setup(props) {
    const uId = computed(() => props.navitem);
    return {
      uId,
    };
