<template>
  <div id="Tips" class="layout">
    <p class="tit">Tips信息提示框（方法 h_tips）</p>
    <div class="box_w w50">
      <div class="fl f2">
        <h-button
          :butContent="'信息提示'"
          :type="'success'"
          @click="tipsFn()"
        />
      </div>
      <div class="fl f2">
        <h-button
          :butContent="'标题信息提示'"
          :type="'success'"
          @click="tipsFn1()"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { getCurrentInstance } from "vue";
export default {
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    // 信息提示框按键展示
    const tipsFn = function () {
      // 自定义弹框
      proxy.h_tips({
        type: "success",
        msg: "提示信息文字",
        top: 100,
        time: 100,
      });
    };
    // 标题信息提示框按键展示
    const tipsFn1 = function () {
      // 自定义弹框
      proxy.h_tips({
        type: "success",
        tit: "提示标题",
        msg: "提示信息文字",
        top: 30,
        time: 1000,
      });
    };
    return {
      tipsFn,
      tipsFn1,
    };
  },
};
</script>

<style lang="less" scoped></style>
