<template>
  <div id="Confirm" class="layout">
    <p class="tit">Confirm 确认提示框（方法 h_confirm）</p>
    <h-button
      :butContent="'确认提示框'"
      :fontSize="14"
      :width="'200px'"
      @click="ConfirmFn()"
    />
    <!-- 以下是参数说明 -->
    <p class="tit">Options 配置</p>
    <h-formtable :contentData="contentData" />
    <p class="tit">Options style box</p>
    <h-formtable :contentData="contentData_box" />
    <p class="tit">Options style title</p>
    <h-formtable :contentData="contentData_title" />
    <p class="tit">Options style content</p>
    <h-formtable :contentData="contentData_content" />
    <p class="tit">Options buttonConfigs</p>
    <h-formtable :contentData="contentData_button" />
  </div>
</template>

<script>
import { getCurrentInstance } from "vue";
export default {
  setup() {
    const { proxy } = getCurrentInstance();
    const contentData = [
      ["title", "提示标题", "String", "——", "标题"],
      ["content", "提示内容", "String", "——", "提示信息"],
    ];
    const contentData_box = [
      ["top", "顶部距离", "Number", "——", "50"],
      ["bgColor", "背景色", "String", "——", "#fff"],
      ["width", "宽度", "Number", "——", "300"],
      ["padding", "内边距（不支持单边设置）", "Number", "——", "10"],
      ["borderRadius", "圆角", "Number", "——", "2"],
    ];
    const contentData_title = [
      ["titTextAlign", "字体水平位置", "String", "left、right", "center"],
      ["titColor", "标题颜色", "String", "——", "#000"],
      ["titLineHeight", "标题行高", "Number", "——", "40"],
      ["titFontSize", "标题字号", "Number", "——", "16"],
      ["titFontWeight", "标题粗度", "Number", "——", "700"],
    ];
    const contentData_content = [
      ["conTextAlign", "字体水平位置", "String", "left、right", "center"],
      ["conColor", "标题颜色", "String", "——", "#777"],
      ["conLineHeight", "标题行高", "Number", "——", "22"],
      ["conFontSize", "标题字号", "Number", "——", "14"],
      ["conFontWeight", "标题粗度", "Number", "——", "100"],
    ];
    const contentData_button = [
      ["yes", "确认（点击执行then）", "String", "——", "确认"],
      ["no", "拒绝（点击执行catch）", "String", "——", "取消"],
    ];
    const ConfirmFn = function () {
      proxy
        .h_confirm({
          title: "确认框",
          content: "可以自定义基本样式和弹框顶部距离，按键文字等...",
          style: {
            box: {
              // top: 100,
              bgColor: "#fff",
              width: 400,
              padding: 10,
              borderRadius: 0,
            },
            title: {
              // titTextAlign:'left',
              // titColor: "red",
              // titLineHeight:30,
              titFontSize: 18,
              // titFontWeight: 100,
            },
            content: {
              // conTextAlign:'left',
              // conColor:'blue',
              // conLineHeight:22,
              // conFontSize:14,
              // conFontWeight:100
            },
          },
          buttonConfigs: {
            yes: "确定",
            no: "取消",
          },
        })
        .then(() => {
          // 自定义弹框
          proxy.h_tips({
            type: "success",
            msg: "确认",
            top: 30,
            time: 1000,
          });
        })
        .catch(() => {
          // 自定义弹框
          proxy.h_tips({
            type: "info",
            msg: " 取消",
            top: 30,
            time: 1000,
          });
        });
    };
    return {
      contentData,
      contentData_box,
      contentData_title,
      contentData_content,
      contentData_button,
      ConfirmFn,
    };
  },
};
</script>

<style lang="less" scoped></style>
