<template>
  <div class="suptopUibox" :class="{ suptopUi_navshow: suptopUi_navSwitch }">
    <!-- 左侧导航 -->
    <div class="suptopUi_nav">
      <!-- 内容列表 自定义滚动条-->
      <!-- :href="'#/assembly/#' + nav.EN"    //#hash方式 加入a标签中-->
      <h-scrollContainer>
        <a
          class="navitem"
          @click="navChange(nav.EN)"
          :class="{ active: navitem == nav.EN }"
          v-for="nav in navlist"
          :key="nav.EN"
          >{{ nav.EN }} {{ nav.CH }}</a
        >
      </h-scrollContainer>
    </div>
    <!-- 右侧组件展示 -->
    <div class="suptopUi_components">
      <!-- 控制导航显示隐藏按键 -->
      <span
        class="suptopUi_navbtn iconfont icon-24px"
        @click="suptopUi_navSwitch = !suptopUi_navSwitch"
      ></span>
      <!-- 右侧组件展示 自定义滚动条-->
      <h-scrollContainer>
        <!-- 组件展示列表 -->
        <div class="HuiContent">
          <assemblyList :navitem="navitem" />
        </div>
      </h-scrollContainer>
    </div>
  </div>
</template>
<script>
/*
页面右侧组件展示2种方式，可以打开注释切换
1.#hash给url添加#参数,右侧展示到固定#位置，在一个页面展示所有组件
2.query给url添加?参数,右侧以动态组件形式展示组件，
*/
import { onMounted, ref, reactive } from "vue";
import { useRouter, useRoute } from "vue-router";
// 组件使用展示组件
import assemblyList from "./assembly_list";
import { UiList } from "@/components/component_module/index.js";
export default {
  components: {
    assemblyList,
  },
  setup() {
    const router = useRouter();
    const route = useRoute();
    const suptopUi_navSwitch = ref(false); //导航控制显示隐藏
    const navitem = ref(null); //保存选中导航元素值
    const navlist = reactive(UiList);

    onMounted(() => {
      queryChange(); // ****query参数方式

      // ****打开下面#hash方式，注释上面一行
      // hashChange(); // 页面首次进入及刷新先获取#组件位置  ****#hash方式
      // watchUrlChange(); // 监听url变化   ****#hash方式
    });

    // 首次和点击时改变nav和url参数
    const navChange = function (idName) {
      suptopUi_navSwitch.value = false; //小窗口点击导航就隐藏导航
      navitem.value = idName; //切换选中a标识
      // ****query参数方式
      router.replace({ query: { uid: idName } });
      // ****打开下面#hash方式 ，注释上面一行
      // 问题 a标签锚点 让#不在url上显示，
      // 解决 在代码中跳转
      // document.querySelector("#" + idName).scrollIntoView(true); //'#'+idName   #Drop  锚点跳转   元素id = 'Drop'
    };

    // ****query参数方式
    const queryChange = function () {
      // #/assembly?idName  =   explain
      const loc = route.query.uid;
      // 首次进入url无参数要赋值url参数explain说明展示
      if (!loc) {
        navChange("Explain");
      } else {
        //刷新页面,url有参数就直接改变nav
        navitem.value = loc;
      }

      // navitem.value = loc;
    };

    // ****打开下面#hash方式，注释上面一行queryChange方法
    // // 监听url变化
    // const watchUrlChange = function () {     //****#hash方式
    //   //url变化监听器
    //   if (
    //     "onhashchange" in window &&
    //     (typeof document.documentMode === "undefined" ||
    //       document.documentMode == 8)
    //   ) {
    //     // 浏览器支持onhashchange事件
    //     window.onhashchange = hashChange; // TODO，对应新的hash执行的操作函数
    //   } else {
    //     console.log("浏览器不支持监听url变化");
    //     // 不支持则用定时器检测的办法
    //     // setInterval(function() {
    //     //     // 检测hash值或其中某一段是否更改的函数， 在低版本的iE浏览器中通过window.location.hash取出的指和其它的浏览器不同，要注意
    //     //     var ischanged = isHashChanged();
    //     //     if(ischanged) {
    //     //         hashChange();  // TODO，对应新的hash执行的操作函数
    //     //     }
    //     // }, 150);
    //   }
    // };
    // // url发生变化 =>执行 时获取组件id参数跳转对应位置
    // const hashChange = function () {    //****#hash方式
    //   //刷新页面时  #/assembly/#Button   截取出来 //Button
    //   //首次跳转到页面时，获取不到组件#id，直接定位到顶部说明位置
    //   const loc = location.hash.split("#")[2] ?? "explain";
    //   navChange(loc); //跳转对应位置
    // };

    return {
      suptopUi_navSwitch,
      navitem,
      navlist,
      navChange,
    };
  },
};
</script>
<style lang="less" scoped>
/* 整体页面 */
.suptopUibox {
  position: relative;
  // width: 100%;
  height: 100%;
  box-sizing: border-box;
  .suptopUi_nav {
    width: 350px;
    height: 100%;
    position: absolute;
    padding-top: 10px;
    padding-left: 150px;
    z-index: 888;
    top: 0;
    left: 0;
    box-sizing: border-box;
    overflow: auto;
    transition: @T;
    background: rgb(255, 255, 255);
    border-right: 1px solid #ddd;
    transition: @T;
    &:hover {
      .navitem {
        color: #000;
      }
    }
    .navitem {
      cursor: pointer;
      display: block;
      padding: 0 20px;
      line-height: 40px;
      height: 40px;
      transition: @T;
      color: @ZC;
      box-sizing: border-box;
      overflow: hidden;
      font-size: 14px;
      &:hover,
      &.active {
        color: @TC;
      }
    }
  }
  .suptopUi_components {
    width: 100%;
    height: 100%;
    padding-left: 350px;
    box-sizing: border-box;
    overflow: auto;
    transition: @T;
    .suptopUi_navbtn {
      cursor: pointer;
      font-size: 20px;
      display: none;
      position: absolute;
      transition: @T;
      z-index: 990;
    }
    .HuiContent {
      padding-right: 150px;
      box-sizing: border-box;
      transition: @T;
    }
  }
}
.med {
  .suptopUi_nav {
    width: 200px;
    padding-left: 0px;
  }
  .suptopUi_components {
    padding-left: 200px;
    .HuiContent {
      padding-right: 0px;
    }
  }
}
.min {
  .suptopUi_navshow {
    .suptopUi_nav {
      width: 200px;
    }
    .suptopUi_navbtn {
      padding-left: 200px;
    }
  }
  .suptopUi_nav {
    width: 0;
  }
  .suptopUi_components {
    padding-left: 0px;
    .suptopUi_navbtn {
      display: block;
    }
  }
}
</style>
