<template>
  <!-- 数字动画 -->
  <div id="NumAnimation" class="layout">
    <p class="tit">NumAnimation 数字动画（标签 h-numAnimation）</p>
    <div class="box_w">
      <div class="fl f3">
        <h-numAnimation
          :eleNumber="888"
          :bgColor="'#00a2ff'"
          :numColor="'#fff'"
          :fontSize="40"
          :numSpeed="50"
        />
      </div>
      <div class="fl f3">
        <h-numAnimation
          :eleNumber="777"
          :bgColor="'#e6a23c'"
          :fontSize="40"
          :numColor="'#fff'"
        />
      </div>
      <div class="fl f3">
        <h-numAnimation
          :eleNumber="345"
          :fontSize="40"
          :bgColor="'#f56c6c'"
          :numColor="'#fff'"
          :numSpeed="10"
          :numFrequency="666"
        />
      </div>
    </div>
    <!-- 以下是参数说明 -->
    <p class="tit">Attributes 属性</p>
    <h-formtable :contentData="contentData" />
  </div>
</template>

<script>
export default {
  setup() {
    const contentData = [
      ["eleNumber", "显示的目标数字", "Number", "——", "99"],
      ["bgColor", "数字背景色", "String", "——", "#fff"],
      ["fontSize", "字体大小", "Number", "——", "16"],
      ["numColor", "数字颜色", "String", "——", "#000"],
      ["numSpeed", "数字变化速度（单位 毫秒）", "Number", "——", "100"],
      [
        "numFrequency",
        "数字增长频率（值1时无动画，值>=EleNumber时每次增加1）",
        "Number",
        "——",
        "20",
      ],
    ];
    return {
      contentData,
    };
  },
};
</script>

