<template>
<!-- 未完成 -->
  <div id="Button" class="layout">
    <p class="tit">Button 按钮（标签 h-button）</p>
    <div class="box_w w50">
      <h-button
        :butContent="'灰色'"
        :type="'info'"
        :float="'l'"
        :width="'100px'"
        @click="butfn"
      />
      <h-button
        :butContent="'绿色'"
        :type="'success'"
        :float="'l'"
        :width="'100px'"
        @click="butfn"
      />
      <h-button
        :butContent="'黄色'"
        :type="'warning'"
        :float="'l'"
        :width="'100px'"
        @click="butfn"
      />
      <h-button
        :butContent="'默认'"
        :width="'100px'"
        :float="'l'"
        @click="butfn"
      />
      <h-button
        :butContent="'红色'"
        :type="'error'"
        :float="'l'"
        :width="'100px'"
        @click="butfn"
      />
    </div>
    <!-- 以下是参数说明 -->
    <p class="tit">Attributes 属性</p>
    <h-formtable :contentData="contentData" />
    <p class="tit">Events 事件</p>
    <h-formtable :titData="titDataEvent" :contentData="contentDataEvent" />
  </div>
</template>

<script>
import { getCurrentInstance } from "vue";
export default {
  setup(props) {
    const { proxy } = getCurrentInstance();
    const contentData = [
      ["width", "按键宽度", "String", "px", "auto"],
      ["bgColor", "按键背景颜色", "String", "——", "#00a2ff"],
      ["fontColor", "字体颜色", "String", "——", "#fff"],
      ["fontSize", "字体大小", "Number", "——", "16"],
      ["lineHeight", "内容行高", "Number", "——", "30"],
      ["butContent", "按键内容", "String", "——", "确认"],
      [
        "type",
        "按键类型",
        "String",
        "info、success、warning、error、default",
        "default",
      ],
      ["float", "按键是否浮动", "String", "l、r", "——"],
    ];
    const titDataEvent = ["事件名", "说明", "参数"];
    const contentDataEvent = [["click", "点击触发事件", "type 按键类型"]];
    const butfn = function (type) {
      // 自定义弹框
      proxy.h_tips({
        type: type,
        msg: type + " 类型",
        top: 30,
        time: 1000,
      });
    };
    return {
      contentData,
      titDataEvent,
      contentDataEvent,
      butfn,
    };
  },
};
</script>

<style lang="less" scoped></style>
