<template>
  <div id="ScrollContainer" class="layout">
    <p class="tit">ScrollContainer 滚动条（标签 h-scrollContainer）</p>
    <div class="box_w">
      <div class="fl f2">
        <div class="scroll_box">
          <h-scrollContainer
            :height="'200px'"
            :slotColor="'rgb(185, 227, 255)'"
